.portfolioComp_container{
    height: 550px;
    background-color: rgb(19, 1, 36);
    box-sizing: border-box;
    box-shadow: 50px 10px 100px rgb(95, 4, 181);
}
.portfolioComp_container >div{
    width: 90%;
    height: 100%;
    display: flex;
    flex: 2;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.portfolioComp_container >div div:last-child{
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

/* convert mode */
.portfolioComp_container >div div:last-child>span{
    display: flex;
    width: 500px;
    height: 400px;
    border:10px solid rgb(23, 239, 239);
    border-radius: 5px;
    position:relative;
    left: -100px;
    align-items: center;
    justify-content: center;
}
.portfolioComp_container >div div:last-child>span>img{
    width: 500px;
    height: 400px;
    position: relative;
    top: -40px;
    right: -40px;
    border-radius: 10px;
    box-sizing: border-box;
    box-shadow: 100px 40px 100px hwb(181 2% 9%),-100px 10px 100px rgb(7, 175, 175) ;
}
.portfolioComp_container >div div:first-child{
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}
.portfolioComp_container >div div:first-child >h1{
    width: 100%;
    text-align: left;
    text-indent: 58px;
    font-size: 30px;
    position: relative;
    top: -80px;
    color: aqua;
    font-family:  'Arial Narrow', Arial, sans-serif;
}
.portfolioComp_container >div div:first-child >h2{
    width: 100%;
    text-align: left;
    text-indent: 58px;
    font-size: 25px;
    position: relative;
    top: -70px;
    left: 40px;
    color: aqua;
    font-family:  'Arial Narrow', Arial, sans-serif;
}
.portfolioComp_container >div div:first-child >p{
    width: 350px;
    position: relative;
    top: -50px;
    left: -60px;
    font-family: sans-serif;
    font-weight: 500;
    color: rgb(161, 158, 155);
    box-sizing: border-box;
    padding: 2px;
}
.portfolioComp_container >div div:first-child >div{
    display: block;
    position: absolute;
    bottom: 120px;
}
.portfolioComp_container >div div:first-child >div >button{
    width: 150px;
    height: 40px;
    margin-right: 80px;
    outline: none;
    border: none;
    position: relative;
    border-radius: 10px;
    box-sizing: border-box;
    box-shadow: inset 100px 0px 50px rgb(13, 183, 250),inset -100px -0px 50px rgb(5, 250, 95);
    font-family: serif;
    font-size: 20px;
    font-weight: 400;
    color: white;
}
.portfolioComp_container >div div:first-child >div >button:hover{
    cursor: pointer;
}
@media (max-width:600px){
    .portfolioComp_container{
        /* height: 550px; */
        background-color: rgb(19, 1, 36);
        box-sizing: border-box;
        box-shadow: 50px 10px 100px rgb(95, 4, 181);
        width: 480px;
    }
}