.contactContainer{
    height: 600px;
}
.contactContainerHeading{
    height: 80px;
    color: white;
    text-align: center;
    font-size: 50px;
    letter-spacing: 10px;
}
.contactContainer>div{
    width: 80%;
    height: 600px;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-sizing: border-box;
    box-shadow: inset 0px 0px 1000px rgb(19, 1, 36),100px 100px 1000px rgb(19, 1, 36);
    border-radius: 100px 100px 0px 0px;
    background-color: rgb(95, 215, 215);
}
.contactContainerContDisp{
    display: flex;
    height: 400px;
    flex: 2;
}
.contactContainerContDisp>:first-child{
    flex: 1;
    box-sizing: border-box;
    box-shadow: inset 0px 0px 1000px rgb(19, 1, 36),100px 100px 1000px rgb(19, 1, 36);
    border-radius: 0px 100px 100px 0px;
}
.contactContainerContDisp>:first-child>div{
    width: 60%;
    position: relative;
    left: 30px;
}
.contactContainerContDisp>:first-child>div>span{
    color: white;
    height: 25px;
    line-height: 25px;
    font-size: 20px;
    font-weight: 700;
    font-family: monospace;
}
.contactContainerContDisp>:first-child>div>p{
    font-weight: 400;
    font-style: oblique;
    border-radius: 10px;
    margin-bottom: 50px;
    color: aquamarine;
}
.contactContainerContDisp>:first-child>div>div>span{
    display: inline-block;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    margin-bottom: 10px;
    margin-right: 20px;
    box-sizing: border-box;
    box-shadow: inset 2px 2px 10px aqua;
    border-radius: 20px;
}
.contactContainerContDisp>:first-child>div>div{
    color: white;
}
.contactContainerContDisp>:first-child>div>div>span>:first-child{
    color: white;
    margin-top: 10px;
}
.contactContainerContDisp>:last-child{
    flex: 1;
}
.contactContainerContDisp>:last-child>div{
    width: 80%;
    height: 80%;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border-radius: 20px;
    box-sizing: border-box;
    /* background: linear-gradient(to bottom, rgb(94, 103, 103), rgb(110, 116, 118),rgb(78, 97, 97)); */
    /* box-shadow: 100px 100px 500px rgb(1, 8, 8),-1px -100px 500px rgb(1, 8, 8); */
}
.contactContainerContDisp>:last-child>div>form{
    width: 80%;
    height: 90%;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.contactContainerContDisp>:last-child>div>form>input{
    display: block;
    margin: 10px;
    width: 80%;
}
.contactContainerContDisp>:last-child>div>form>input[type=text]{
    height: 40px;
    background: none;
    border: none;
    outline: none;
    box-sizing: border-box;
    box-shadow: -1px 2px 2px aqua;
    text-indent: 10px;
    text-transform: capitalize;
    color: aquamarine;
    font-size: 15px;
}
.contactContainerContDisp>:last-child>div>form>input[type=text]::placeholder{
    color: aqua;
}
.contactContainerContDisp>:last-child>div>form>input[type=email]{
    height: 40px;
    background: none;
    border: none;
    outline: none;
    box-sizing: border-box;
    box-shadow: -1px 2px 2px aqua;
    text-indent: 10px;
    color: aquamarine;
    font-size: 15px;
}
.contactContainerContDisp>:last-child>div>form>input[type=email]::placeholder{
    color: aqua;
}
.contactContainerContDisp>:last-child>div>form>textarea{
    margin-left: 10px;
    resize: none;
    padding: 2px;
    background: none;
    border: none;
    outline: none;
    box-sizing: border-box;
    box-shadow: -1px 2px 2px aqua;
    text-indent: 10px;
    color: aquamarine;
    font-size: 15px;
}
.contactContainerContDisp>:last-child>div>form>textarea::placeholder{
    color: aqua;
}
.contactContainerContDisp>:last-child>div>form>input[type=submit]{
    width: 100px;
    margin-top: 20px;
    position: inherit;
    left: 90px;
    height: 35px;
    border: none;
    outline: none;
    border-radius: 20px;
    line-height: 35px;
    font-size: 20px;
    font-family: cursive;
    font-weight: 700;
    color: rgb(4, 21, 21);
    box-sizing: border-box;
    box-shadow: inset 10px 10px 10px aqua, inset -10px -10px 50px aquamarine;
}
.contactContainerContDisp>:last-child>div>form>input[type=submit]:hover{
    cursor: pointer;
    background-color: green;
}
.ContactFooterPannel{
    height: 120px;
}
.ContactFooterPannel>hr{
    border: 1px dashed white;
}
.ContactFooterPannel>div{
    width: 100%;
    height: 100%;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex: 3;
    align-items: center;
}
.ContactFooterPannel>div>:first-child{
    flex: 1;
    height: 100%;
    line-height: 100%;
    display: flex;
    align-items: left;
    justify-content: left;
}
.ContactFooterPannel>div>:first-child>img{
    margin-top: 10px;
    width: 150px;
    height: 100px;
    border-radius: 10px;
}
.ContactFooterPannel>div>span{
    display: block;
    flex: 1;
    height: 100px;
}
.ContactFooterPannel>div>span>div{
    width: 80%;
    color: aqua;
    font-size: 15px;
    margin-top: 45px;
    box-sizing: border-box;
    box-shadow: 10px 10px 10px rgb(19, 1, 36);
}
.ContactFooterPannel>div>span>div>#materialCopyLogo{
    font-size: 25px;
    color: rgb(18, 214, 214);
}

.ContactFooterPannel>div>:last-child{
    flex: 1;
    display: flex;
    height: 50px;
}
.ContactFooterPannel>div>:last-child>div{
    width: 50px;
    height: 50px;
    margin-right: 50px;
    border-radius: 10px; 
    box-sizing: border-box;
    box-shadow: inset 2px 1px 30px rgb(19, 1, 36);
    display: flex;
    align-items: center;
    justify-content: center;
}
.ContactFooterPannel>div>:last-child>div>.mateClasslogo{
    font-size: 40px;
    margin: 4px;
    color: aqua;
}
