.experienceContainer{
    width: 100%;
    height: 600px;
}
.experienceContainer>:first-child{
    color: white;
    text-align: center;
    height: 30px;
    font-size: 30px;
    letter-spacing: 10PX;
    margin-bottom: 10px;
}
.experienceContainer_firstflex{
    display: flex;
    align-items: center;
    justify-content: center;
}
.experienceContainer_firstflex>div>img{
    width:80px;
    height: 80px;
    border-radius: 50%;
    margin: 60px;
    padding: 2px;
}
.experienceContainer_firstflex>div>span{
    display: block;
    color: white;
    position: relative;
    left: 10px;
    top: -40px;
    text-align: center;
    height: 30px;
    line-height: 30px;
    font-size: 30px;
    letter-spacing: 5px;
}
@media (max-width:600px){
    .experienceContainer{
        width: 100%;
        margin-top: 400px;
        height: 2200px;
    }
    .experienceContainer>:first-child{
        color: white;
        text-align: center;
        height: 30px;
        font-size: 30px;
        letter-spacing: 10PX;
        margin-bottom: 10px;
    }
    .experienceContainer_firstflex{
        display: flex;
        flex-direction:column-reverse;
        align-items: center;
        justify-content: center;
    }
    .experienceContainer_firstflex>div>img{
        width:80px;
        height: 80px;
        border-radius: 50%;
        margin: 60px;
        padding: 2px;
    }
    .experienceContainer_firstflex>div>span{
        display: block;
        color: white;
        position: relative;
        left: 10px;
        top: -40px;
        text-align: center;
        height: 30px;
        line-height: 30px;
        font-size: 30px;
        letter-spacing: 5px;
    }
}
