.titleBodyContainer{
    height: 400px;
}
.titleBodyContainer div{
    height: 99%;
}
.titleBodyContainer div h1{
    margin: 80px 0px 0px 10px;
    padding: 2px;
    font-size: 50px;
    color: rgb(152, 148, 148);
    text-indent: 30px;
}
.titleBodyContainer div p{
    margin-left: 10px;
    padding: 2px;
    font-size: 60px;
    font-family: red serifs;
    color: aliceblue;
    font-weight: 700;
    letter-spacing: 1px;
    text-rendering: geometricPrecision;
    padding-left: 30px;
}
.titleBodyContainer div button{
    margin-top: 40px;
    width: 200px;
    height: 40px;
    margin-left: 10px;
    position: relative;
    left: 100px;
    border-radius: 10px;
    outline: none;
    border: none;
    box-sizing: border-box;
    box-shadow: inset 10px 0px 25px rgb(13, 183, 250),inset -10px -0px 25px rgb(14, 254, 102);
    font-family: serif;
    font-size: 20px;
    font-weight: 700;
    background: linear-gradient(to top, rgb(197, 11, 11), rgb(245, 245, 81));
    background-clip: text;
    color: transparent;
}
.titleBodyContainer div button:hover{
    cursor: pointer;
    box-shadow: inset 1px 0px 1px rgb(13, 183, 250),inset -1px -0px 1px rgb(14, 254, 102);
    animation-name: slide;
    animation-duration: 4s;
    background-color: black;
    animation-timing-function: cubic-bezier(0.785, 10, 0.15, 0.86);
    animation-iteration-count: infinite;
    animation-direction: reverse;
}
@keyframes slide {
    from{
        box-shadow: inset 30px 10px 50px rgb(13, 183, 250),inset -30px -10px 50px rgb(14, 254, 102);
    }
    to{
        box-shadow: inset 1px 0px 10px rgb(13, 183, 250),inset -1px -0px 1px rgb(14, 254, 102);
    }
}
@media (max-width:600px){
    .titleBodyContainer{
        height: 1000px;
    }
}