.portNavigationBarConta{
    width: 100%;
}
.portNavigationBarConta div{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 20px;
}
.portNavigationBarConta div span{
    display: inline-block;
    margin: 10px 30px 10px 30px;
    width: 200px;
    text-align: center;
    background: linear-gradient(to right, red,aqua,yellow);
    background-clip: text;
    color: transparent;
    padding: 2px;
    font-weight: 700;
    box-sizing: border-box;
}
.portNavigationBarConta div span:hover{
    cursor: pointer;
    animation-name: chenging;
    animation-duration: 2s;
    animation-timing-function: cubic-bezier(30, 80, 5, 1);
    animation-iteration-count: infinite;
}
@keyframes chenging {
    0%{
        background: linear-gradient(to right, aqua, red);
        background-clip: text;
        color: transparent;
        border-left: 2px solid yellow;
        border-radius: 100%;
    }
    100%{
        background: linear-gradient(to right, yellow, aqua);
        background-clip: text;
        color: transparent;
        border-right: 2px solid red;
        border-radius: 100%;
    }
}