.serviceContainer{
    width: 100%;
    height: 800px;
}
.serviceContainer>div:first-child{
    color: white;
    text-align: center;
    height: 30px;
    line-height: 30px;
    font-size: 30px;
    letter-spacing: 2px;
    font-weight: 500;
    margin-top: 15px;
}
.serviceContainer>div:last-child{
    color: aliceblue;
    width: 80%;
    height: 90%;
    background-color: rgb(19, 1, 36);
    position: relative;
    top:50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.serviceContainer_firstflex{
    display: flex;
    flex: 2;
    height: 290px;
    text-align: center;
    justify-content: center;
    align-items: center;
}
.serviceContainer_firstflex>:first-child{
    flex: 1;
}
.serviceContainer_firstflex>:first-child>:first-child{
    width: 300px;
    height: 250px;
    position: relative;
    left: 40%;
    box-sizing: border-box;
    padding: 10px;
    box-shadow:  5px 5px 25px aqua, -5px -5px 50px aqua,inset 5px 5px 1px rgb(26, 80, 227),inset -5px -5px 1px rgb(25, 85, 249);
    border-radius: 10px;
}
.serviceContainer_firstflex>:first-child>:first-child>img{
    width:50px;
    height: 50px;
    border-radius: 50%;
    position: relative;
    left: -10px;
    top: 10px;
}
.serviceContainer_firstflex>:first-child>:first-child>span{
    display: inline-block;
    position: relative;
    font-size: 1.5em;
    font-weight: 700;
    top: -10px;
}
.serviceContainer_firstflex>:first-child>:first-child>div{
    position: relative;
    top: 10px;
    left: 15px;
    width: 250px;
    height: 150px;
}
.serviceContainer_firstflex>:last-child{
    flex: 1;
}
.serviceContainer_firstflex>:last-child>:last-child{
    width: 300px;
    height: 250px;
    position: relative;
    right: -10%;
    box-sizing: border-box;
    padding: 10px;
    box-shadow:  5px 5px 25px aqua, -5px -5px 50px aqua,inset 5px 5px 1px rgb(26, 80, 227),inset -5px -5px 1px rgb(25, 85, 249);
    border-radius: 10px;
}
.serviceContainer_firstflex>:last-child>:last-child>img{
    width:50px;
    height: 50px;
    border-radius: 50%;
    position: relative;
    left: -10px;
    top: 10px;
}
.serviceContainer_firstflex>:last-child>:last-child>span{
    display: inline-block;
    position: relative;
    font-size: 1.5em;
    font-weight: 700;
}
.serviceContainer_firstflex>:last-child>:last-child>div{
    position: relative;
    top: 10px;
    left: 15px;
    width: 250px;
    height: 150px;
}
