.abourMeContainer{
    height: 550px;
    /* box-sizing: border-box;
    box-shadow: inset 10px 10px 100px rgb(65, 7, 121),inset -10px -10px -100px rgb(65, 7, 121); */
}
.abourMeContainer >div{
    width: 90%;
    height: 100%;
    display: flex;
    flex: 2;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.abourMeContainer >div div:first-child{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;

}
.abourMeContainer >div div:first-child>span{
    display: flex;
    width: 300px;
    height: 400px;
    border:10px solid rgb(23, 239, 239);
    border-radius: 5px;
    position:relative;
    right: -100px;
    align-items: center;
    justify-content: center;
}
.abourMeContainer >div div:first-child>span>img{
    width: 100%;
    height: 100%;
    position: relative;
    top: -40px;
    left: -40px;
    border-radius: 10px;
    box-sizing: border-box;
    box-shadow: 10px 10px 100px aqua,-10px -10px 100px aqua;
    
}
.abourMeContainer >div div:last-child{
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}
.abourMeContainer >div div:last-child >h1{
    width: 100%;
    text-align: left;
    text-indent: 58px;
    font-size: 25px;
    position: relative;
    top: -20px;
    color: white;
    font-family:  'Arial Narrow', Arial, sans-serif;
}
.abourMeContainer >div div:last-child >p{
    width: 350px;
    position: relative;
    top: -20px;
    left: -60px;
    font-family: sans-serif;
    font-weight: 500;
    color: rgb(161, 158, 155);
    box-sizing: border-box;
    padding: 2px;
}
.abourMeContainer >div div:last-child >div{
    display: block;
    position: absolute;
    bottom: 120px;
}
.abourMeContainer >div div:last-child >div >button{
    width: 150px;
    height: 40px;
    margin-right: 80px;
    outline: none;
    border: none;
    position: relative;
    border-radius: 10px;
    box-sizing: border-box;
    box-shadow: inset 100px 0px 50px rgb(13, 183, 250),inset -100px -0px 50px rgb(5, 250, 95);
    font-family: serif;
    font-size: 20px;
    font-weight: 400;
    color: white;
}
.abourMeContainer >div div:last-child >div >button:last-child{
    background-color: transparent;
    background: linear-gradient(to  left, rgb(240, 99, 99),rgb(5, 250, 95));
    background-clip: text;
    font-weight: 700;
    color: transparent;
    box-shadow: inset 2px 2px 0px rgb(13, 183, 250),inset -2px -2px 0px rgb(5, 250, 95);
}
.abourMeContainer >div div:last-child >div >button:hover{
    cursor: pointer;
}
@media (max-width:600px){
    .abourMeContainer{
        display: block;
        margin-top: -160px;
        margin-bottom: 100px;
    }
    .abourMeContainer >div{
        display: block;
        height: 900px;
    }
    .abourMeContainer >div div:first-child{
        display: block;
        
    }
    .abourMeContainer >div div:first-child>span{
        width: 300px;
        height: 400px;
        border:10px solid rgb(23, 239, 239);
        border-radius: 5px;
        
    }
    .abourMeContainer >div div:first-child>span>img{
        display: block;
        width: 100%;
        height: 100%;
        border-radius: 10px;
        box-sizing: border-box;
        box-shadow: 10px 10px 100px aqua,-10px -10px 100px aqua;
        
        
    }
    .abourMeContainer >div div:last-child{
        display: flex;
    }
    .abourMeContainer >div div:last-child >h1{
        display: block;
        width: 100%;
        text-align: left;
        text-indent: 58px;
        font-size: 25px;
        color: white;
        font-family:  'Arial Narrow', Arial, sans-serif;
        margin-top: 25px;
    }
    .abourMeContainer >div div:last-child >p{
        font-family: sans-serif;
        font-weight: 500;
        color: rgb(161, 158, 155);
        box-sizing: border-box;
        padding: 2px;
        margin-left: 10px;
    }
    .abourMeContainer >div div:last-child >div{
        display: flex;
    }
    .abourMeContainer >div div:last-child >div >button{
        width: 150px;
        height: 40px;
        outline: none;
        border: none;
        border-radius: 10px;
        box-sizing: border-box;
        box-shadow: inset 100px 0px 50px rgb(13, 183, 250),inset -100px -0px 50px rgb(5, 250, 95);
        font-family: serif;
        font-size: 20px;
        font-weight: 400;
        color: white;
        display: inline-block;
        
    }
    .abourMeContainer >div div:last-child >div >button:last-child{
        background-color: transparent;
        background: linear-gradient(to  left, rgb(240, 99, 99),rgb(5, 250, 95));
        background-clip: text;
        font-weight: 700;
        color: transparent;
        margin-top: 10px;
        box-shadow: inset 2px 2px 0px rgb(13, 183, 250),inset -2px -2px 0px rgb(5, 250, 95);
    }
    .abourMeContainer >div div:last-child >div >button:hover{
        cursor: pointer;
    }
}