
.navigationBarContainer{
    padding: 2px;
    margin-bottom: 10px;
    height: 40px;
    margin-top: 20px;
}
.navigationBarContainer div:last-child{
    display: flex;
    flex: 7;
}
.navigationBarContainer  span{
    flex: 1;
    display: block;
    height: 40px;
    line-height: 40px;
    margin-right: 10px;
    text-align: center;
    letter-spacing: 1px; 
    color: rgb(245, 234, 234);
    border-radius: 10px;
}
.navigationBarContainer  span img{
    width: 150px;
    height: 75px;
    position: relative;
    top: -25px;
    border-radius: 10px;
    box-sizing: border-box;
    box-shadow: 2px 2px 100px aqua, -2px -2px 100px aqua;
}
.navigationBarContainer  span:last-child{
    position: relative;
    border-radius: 10px;
    box-sizing: border-box;
    box-shadow: inset 100px 0px 50px rgb(13, 183, 250),inset -100px -0px 50px rgb(5, 250, 95);
    font-family: serif;
    font-size: 20px;
    font-weight: 400;
    color: white;
}
.navigationBarContainer  span:last-child:hover{
    border-radius: 20px;
}
.navigationBarContainer  span:hover{
    cursor: pointer;
    border-bottom: 1px solid silver;
    border-radius: 100%;
   
}
@media (max-width:600px){
    .navigationBarContainer{
        padding: 2px;
        margin-bottom: 10px;
        height: 40px;
        margin-top: 20px;
        height: fit-content;
    }
    .navigationBarContainer div:last-child{
        display: block;
    }
    .navigationBarContainer  span{
        display: block;
        height: 40px;
        align-items:center;
        justify-content: center;
        letter-spacing: 1px; 
        color: rgb(245, 234, 234);
        border-radius: 10px;
        margin-bottom: 10px;
    }
    .navigationBarContainer  span img{
        width: 150px;
        height: 75px;
        position: relative;
        top: -25px;
        border-radius: 10px;
        box-sizing: border-box;
        box-shadow: 2px 2px 100px aqua, -2px -2px 100px aqua;
    }
    .navigationBarContainer  span:last-child{
        border-radius: 10px;
        box-sizing: border-box;
        box-shadow: inset 100px 0px 50px rgb(13, 183, 250),inset -100px -0px 50px rgb(5, 250, 95);
        font-family: serif;
        font-size: 20px;
        font-weight: 400;
        color: white;
    }
    .navigationBarContainer  span:last-child:hover{
        border-radius: 0px;
    }
    .navigationBarContainer  span:hover{
        cursor: pointer;
        border-bottom: 1px solid silver;
        border-radius: 10%;
       
    }
}